.login-form {
  width: 300px;
  color: #646c85;
  background-color: #10172f;

  &__header {
    margin: 0;
    color: white;
    font-size: 32px;
  }

  &__text {
    font-size: 16px;
    margin: 10px 0;
  }

  &__form {
    display: flex;
    flex-flow: column;
  }

  &__email,
  &__password {
    color: white;
    font-weight: bold;
    font-size: 17px;
    border-bottom: 1px solid #333a52;
    padding: 15px 0;
    margin-top: 20px;
  }

  &__email:focus,
  &__password:focus {
    border-bottom: 1px solid #4dbcfd;
    transition: all 300ms;
  }

  &__email:valid,
  &__password:valid {
    border-bottom: 1px solid #21be9a;
    transition: all 300ms;
  }

  &__email::placeholder,
  &__password::placeholder {
    color: #4f5b77;
    font-weight: bold;
    font-size: 17px;
  }

  &__link {
    color: #008ada;
    font-weight: bold;
  }

  &__button {
    color: white;
    position: relative;
    border-radius: 5px;
    border: none;
    background: linear-gradient(120deg,
    rgba(0,107,217,1) 20%,
    rgba(38,199,251,1) 80%);
    cursor: pointer;
    z-index: 1;
    margin: 30px 0 20px;
    font-weight: bold;
    height: 50px;
  }

  &__button::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    border-radius: 5px;
    background: linear-gradient(120deg,
    rgba(38,199,251,1) 20%,
    rgba(0,107,217,1) 80%);
    opacity: 0;
    transition: all 300ms ease-in;
  }

  &__button:hover::after {
    opacity: 1;
  }

  &__forgot {
    font-size: 16px;
  }

  &__link {
    margin-left: 5px;
  }
}
